@import url("https://fonts.googleapis.com/css2?family=Montserrat&family=Poppins&display=swap");

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

.hero {
  width: 100%;
  height: 100vh;
  background-color: #f5db7a;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.reveal {
  display: flex;
  perspective: 1000px;
}

.reveal button {
  color: #fff;
  padding: 15px 30px;
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7f00,
    #ffff00,
    #ff7f00,
    #ff0000
  );
  background-size: 400% 400%;
  text-transform: uppercase;
  position: relative;
  animation: gradient 5s ease infinite;
  transition: transform 0.2s;
  text-transform: uppercase;
  padding: 10px 60px;
  font-size: 4rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-family: "Poppins", sans-serif;
}

.reveal button:hover {
  transform: scale(1.02);
}

.reveal button:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: inherit;
  z-index: -1;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.timer {
  color: black;
  font-size: 10rem;
  font-family: "Montserrat", sans-serif;
}
